import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom

const FooterContainer = styled.footer`
  background-color: #000;
  color: #fff;
  padding: 2rem;
  text-align: center;
`;

const FooterContent = styled.div`
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
`;

const FooterSection = styled.div`
  margin: 1rem;
`;

const FooterTitle = styled.h3`
  margin-bottom: 1rem;
`;

const FooterLink = styled(Link)`  /* Replace <a> with <Link> */
  color: #fff;
  text-decoration: none;
  display: block;
  margin-bottom: 0.5rem;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -2px;
    left: 0;
    background-color: #fff;
    transition: width 0.3s ease;
  }

  &:hover::after {
    width: 100%;
  }
`;

function Footer() {
  return (
    <FooterContainer>
      <FooterContent>
        <FooterSection>
          <FooterTitle>Quick Links</FooterTitle>
          <FooterLink to="/">Home</FooterLink> {/* Update href to to for Link */}
          <FooterLink to="/about">About Us</FooterLink>
          <FooterLink to="/services">Services</FooterLink>
          <FooterLink to="/contact">Contact</FooterLink>
        </FooterSection>
        <FooterSection>
          <FooterTitle>Contact Us</FooterTitle>
          <p>Email: platinumsolutionsmktg@gmail.com</p>
          <p>Phone: (123) 999-999</p>
        </FooterSection>
        <FooterSection>
          <FooterTitle>Follow Us</FooterTitle>
          <FooterLink as="a" href="#">Facebook</FooterLink> {/* These external links remain unchanged */}
          <FooterLink as="a" href="#">Twitter</FooterLink>
          <FooterLink as="a" href="#">LinkedIn</FooterLink>
        </FooterSection>
      </FooterContent>
      <p>&copy; 2024 Platinum Solutions Marketing. All rights reserved.</p>
    </FooterContainer>
  );
}

export default Footer;
