import React, { useState } from 'react';
import styled from 'styled-components';
import logo from '../logo.png'; // Ensure this path is correct

// Set up container with the background GIF
const PageContainer = styled.div`
  color: #ffffff;
  padding: 4rem;
  height: 100vh;
  background: url(${process.env.PUBLIC_URL + '/background2.gif'}) no-repeat center center fixed;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const FormSection = styled.section`
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 10px;
  padding: 3rem;
  width: 85%;
  max-width: 1200px;
`;

const Logo = styled.img`
position: fixed;
top: -50px;
left: -50px;
z-index: 10;
width: 275px;
height: auto;
`;

const FormContentContainer = styled.div`
  display: flex;
  gap: 3rem; /* Adjusting the gap for better layout */
`;

const LeftColumn = styled.div`
  flex: 1;
`;

const RightColumn = styled.form`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1.2rem; /* Reducing the gap to make fields more compact */
`;

const Input = styled.input`
  padding: 0.8rem; /* Decreased padding to make fields smaller */
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 5px;
  font-size: 1rem; /* Reduced font size */
`;

const Textarea = styled.textarea`
  padding: 0.8rem;
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 5px;
  font-size: 1rem;
  min-height: 100px; /* Reduced height */
`;

const Select = styled.select`
  padding: 0.8rem;
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  border-radius: 5px;
  font-size: 1rem;
`;

const Button = styled.button`
  padding: 0.8rem;
  background-color: #90e0ef;
  width: 150px;
  border: none;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.4s ease, transform 0.4s ease;

  &:hover {
    background-color: #0077b6;
    transform: scale(1.05); /* Slightly enlarges the button */
  }
`;

const LargeTitle = styled.h1`
  font-size: 2.5rem;
  line-height: 1.2;
`;

const Subtitle = styled.p`
  font-size: 1.2rem;
  margin-bottom: 2rem;
`;

const Step = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
`;

const StepNumber = styled.div`
  font-size: 2rem;
  font-weight: bold;
  margin-right: 1rem;
`;

const StepText = styled.div`
  font-size: 1.1rem;
  line-height: 1.5;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding-bottom: 0.5rem;
  flex: 1;
`;

function ContactSection() {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    companyURL: '',
    seoBudget: '',
    marketingBudget: '',
    message: ''
  });

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Form Submitted:', formData);
  };

  return (
    <PageContainer>
      <Logo src={logo} alt="PSM Logo" />
      <FormSection>
        <FormContentContainer>
          {/* Left Column with Title and Descriptions */}
          <LeftColumn>
            <LargeTitle>How can we help you create impact?</LargeTitle>
            <Subtitle>Tell us about yourself and our Sales Team will reach out with next steps.</Subtitle>
            <Step>
              <StepNumber>01</StepNumber>
              <StepText>Identify the 5% of SEO work that drives 95% of the results.</StepText>
            </Step>
            <Step>
              <StepNumber>02</StepNumber>
              <StepText>Build velocity with a custom solution powered by our proprietary platform.</StepText>
            </Step>
            <Step>
              <StepNumber>03</StepNumber>
              <StepText>Establish authority, create defensible distribution, and grow sustainable revenue.</StepText>
            </Step>
          </LeftColumn>

          {/* Right Column with Form */}
          <RightColumn onSubmit={handleSubmit}>
            <Input
              type="text"
              name="firstName"
              placeholder="First name"
              value={formData.firstName}
              onChange={handleChange}
            />
            <Input
              type="text"
              name="lastName"
              placeholder="Last name"
              value={formData.lastName}
              onChange={handleChange}
            />
            <Input
              type="email"
              name="email"
              placeholder="Work email"
              value={formData.email}
              onChange={handleChange}
            />
            <Input
              type="url"
              name="companyURL"
              placeholder="Company URL"
              value={formData.companyURL}
              onChange={handleChange}
            />
            <Select
              name="seoBudget"
              value={formData.seoBudget}
              onChange={handleChange}
            >
              <option value="" disabled>
                Monthly SEO budget
              </option>
              <option value="1000-5000">$1,000 - $5,000</option>
              <option value="5000-10000">$5,000 - $10,000</option>
            </Select>
            <Select
              name="marketingBudget"
              value={formData.marketingBudget}
              onChange={handleChange}
            >
              <option value="" disabled>
                Annual marketing budget
              </option>
              <option value="10000-50000">$10,000 - $50,000</option>
              <option value="50000-100000">$50,000 - $100,000</option>
            </Select>
            <Textarea
              name="message"
              placeholder="Your space to ask questions or share anything else."
              value={formData.message}
              onChange={handleChange}
            />
            <Button type="submit">Contact Us</Button>
          </RightColumn>
        </FormContentContainer>
      </FormSection>
    </PageContainer>
  );
}

export default ContactSection;
